import React, { useState, useEffect } from 'react';
import { useAuth } from '../../common/context/AuthContext';
import { useUnifiedToast } from '../../common/services/unifiedToastService';
import {
  UnifiedBackground,
  UnifiedCard,
  UnifiedButton,
  useScrollAnimation,
  SharedStyles
} from "../../common/components/SharedTheme";
import { supabase } from '../../common/services/supabaseClient';
import {
  Calendar,
  DollarSign,
  CheckCircle,
  XCircle,
  Clock
} from 'lucide-react';

function CustomerPayments() {
  const { user } = useAuth();
  const { showSuccess, showError } = useUnifiedToast();
  useScrollAnimation();

  const [payments, setPayments] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    loadPayments();
  }, [user]);

  const loadPayments = async () => {
    try {
      setLoading(true);
      
      const { data, error } = await supabase
        .from('customer_payments')
        .select('*')
        .eq('customer_id', user.id)
        .order('month_number', { ascending: true });

      if (error) throw error;
      
      setPayments(data || []);
    } catch (error) {
      showError('Failed to load payment history: ' + error.message);
    } finally {
      setLoading(false);
    }
  };

  const getStatusIcon = (status) => {
    switch (status) {
      case 'paid':
        return <CheckCircle className="w-5 h-5 text-green-400" />;
      case 'unpaid':
        return <XCircle className="w-5 h-5 text-red-400" />;
      case 'pending':
        return <Clock className="w-5 h-5 text-yellow-400" />;
      default:
        return <Clock className="w-5 h-5 text-gray-400" />;
    }
  };

  const getStatusColor = (status) => {
    switch (status) {
      case 'paid':
        return 'bg-green-500/20 text-green-300 border-green-500/30';
      case 'unpaid':
        return 'bg-red-500/20 text-red-300 border-red-500/30';
      case 'pending':
        return 'bg-yellow-500/20 text-yellow-300 border-yellow-500/30';
      default:
        return 'bg-gray-500/20 text-gray-300 border-gray-500/30';
    }
  };

  const stats = {
    total: payments.length,
    paid: payments.filter(p => p.status === 'paid').length,
    pending: payments.filter(p => p.status === 'pending').length,
    unpaid: payments.filter(p => p.status === 'unpaid').length,
    totalAmount: payments.reduce((sum, p) => sum + (p.payment_amount || 0), 0)
  };

  if (loading) {
    return (
      <>
        <SharedStyles />
        <UnifiedBackground>
          <div className="min-h-screen flex items-center justify-center">
            <div className="animate-spin rounded-full h-32 w-32 border-b-2 border-blue-400"></div>
          </div>
        </UnifiedBackground>
      </>
    );
  }

  return (
    <>
      <SharedStyles />
      <UnifiedBackground>
        <div className="min-h-screen pt-24 px-8 pb-8">
          <div className="max-w-7xl mx-auto">
            {/* Header */}
            <div className="mb-4">
              <h1 className="text-3xl font-bold text-white mb-2">Payment History</h1>
              <p className="text-gray-400">View and manage your payment records</p>
            </div>

          {/* Stats Cards */}
          <div className="grid grid-cols-2 md:grid-cols-4 gap-3 mb-6">
            <UnifiedCard className="bg-gradient-to-br from-blue-700/30 to-blue-800/30 border border-blue-500/20">
              <div className="p-3 text-center">
                <div className="w-6 h-6 bg-blue-500/30 rounded-full flex items-center justify-center mx-auto mb-2">
                  <Calendar className="w-3 h-3 text-blue-300" />
                </div>
                <h3 className="text-sm font-bold text-white mb-1">{stats.total}</h3>
                <p className="text-blue-200 text-xs">Total Months</p>
              </div>
            </UnifiedCard>

            <UnifiedCard className="bg-gradient-to-br from-green-700/30 to-green-800/30 border border-green-500/20">
              <div className="p-3 text-center">
                <div className="w-6 h-6 bg-green-500/30 rounded-full flex items-center justify-center mx-auto mb-2">
                  <CheckCircle className="w-3 h-3 text-green-300" />
                </div>
                <h3 className="text-sm font-bold text-white mb-1">{stats.paid}</h3>
                <p className="text-green-200 text-xs">Paid Months</p>
              </div>
            </UnifiedCard>

            <UnifiedCard className="bg-gradient-to-br from-yellow-700/30 to-yellow-800/30 border border-yellow-500/20">
              <div className="p-3 text-center">
                <div className="w-6 h-6 bg-yellow-500/30 rounded-full flex items-center justify-center mx-auto mb-2">
                  <Clock className="w-3 h-3 text-yellow-300" />
                </div>
                <h3 className="text-sm font-bold text-white mb-1">{stats.pending}</h3>
                <p className="text-yellow-200 text-xs">Pending Months</p>
              </div>
            </UnifiedCard>

            <UnifiedCard className="bg-gradient-to-br from-purple-700/30 to-purple-800/30 border border-purple-500/20">
              <div className="p-3 text-center">
                <div className="w-6 h-6 bg-purple-500/30 rounded-full flex items-center justify-center mx-auto mb-2">
                  <DollarSign className="w-3 h-3 text-purple-300" />
                </div>
                <h3 className="text-sm font-bold text-white mb-1">₹{stats.totalAmount.toLocaleString()}</h3>
                <p className="text-purple-200 text-xs">Total Amount</p>
              </div>
            </UnifiedCard>
          </div>

          {/* Payments Table */}
          <UnifiedCard>
            <div className="overflow-x-auto">
              <table className="w-full">
                <thead className="bg-gray-700">
                  <tr>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-300 uppercase tracking-wider">Month</th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-300 uppercase tracking-wider">Status</th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-300 uppercase tracking-wider">Amount</th>
                  </tr>
                </thead>
                <tbody className="divide-y divide-gray-700">
                  {payments.length === 0 ? (
                    <tr>
                      <td colSpan="3" className="px-6 py-12 text-center text-gray-400">
                        No payment records found
                      </td>
                    </tr>
                  ) : (
                    payments.map((payment) => (
                      <tr key={payment.id} className="hover:bg-gray-700">
                        <td className="px-6 py-4 whitespace-nowrap">
                          <div className="flex items-center">
                            <div className="w-8 h-8 bg-gradient-to-r from-blue-400 to-blue-500 rounded-full flex items-center justify-center mr-3">
                              <span className="text-white text-sm font-bold">{payment.month_number}</span>
                            </div>
                            <span className="text-white font-medium">Month {payment.month_number}</span>
                          </div>
                        </td>
                        <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-300">
                          <div className={`inline-flex items-center px-2.5 py-1 rounded-full text-xs font-medium transition-all ${getStatusColor(payment.status)}`}>
                            {getStatusIcon(payment.status)}
                            <span className="ml-2 capitalize">{payment.status}</span>
                          </div>
                        </td>
                        <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-300">
                          <span className="font-medium text-white">
                            {payment.payment_amount ? `₹${payment.payment_amount.toLocaleString()}` : 'N/A'}
                          </span>
                        </td>
                      </tr>
                    ))
                  )}
                </tbody>
              </table>
            </div>
          </UnifiedCard>
        </div>
      </div>
    </UnifiedBackground>
  </>
);
}

export default CustomerPayments;
